<template>
  <div id="title">
    <div class="title">
        <span></span>
        {{title}}
    </div>
  </div>
</template>

<script>
export default {
  name: 'title',
  data () {
    return {
    }
  },
  props:[
      'title'
  ]
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less'>
#title{
    .title{
        background: #e8e4e4;
        height: 18px;;
        line-height: 18px;;
        padding: 10px 6px;

        span{
            display: inline-block;
            height: 16px;
            width: 3px;
            background: #157fc2;
            margin-right: 5px;
            vertical-align: middle;
        }
    }
}
</style>



