              >据了解，此次加铺大象静音型超薄沥青路面的西安绕城高速公路示范段于2000年通车，至今已有19年年限，原路面已出现严重的纵横裂缝、车辙、网裂、坑槽等病害，采用传统的方法进行更新改造，多为先铣刨原路面，再加铺厚度为4cm以上的普通沥青路面，铣刨过程不仅造成严重的环境污染，多次铣刨还会对原路面结构产生损害，采用大象静音型超薄沥青路面技术进行更新改造，能最大化的减少环境污染以及施工难度、延长道路使用寿命、增加行车安全性以及舒适性，这得益于大象静音型超薄沥青路面具有以下七大特点。</span>
