import Vue from 'vue'
import Router from 'vue-router'
import home from '@/pages/home'
import aboutus from '@/pages/about/about'
import companygy from '@/pages/aboutdx/companygy'
import companyry from '@/pages/aboutdx/companyry'
import companyzxns from '@/pages/aboutdx/companyzxns'
import intro from '@/pages/aboutdx/intro'
import newsgs from '@/pages/company/newsgs'
import newshy from '@/pages/company/newshy'
import beforeEighteen from '@/pages/example/beforeEighteen'
import nineteen from '@/pages/example/nineteen'
import productsjs from '@/pages/products/productsjs'
import productsspjs from '@/pages/products/productsspjs'
import productsys from '@/pages/products/productsys'
import techlq from '@/pages/tech/techlq'
import techzlrz from '@/pages/tech/techzlrz'
import techzm from '@/pages/tech/techzm'

Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        name: 'home',
        component: home,
    }, {
        path: '/home',
        name: 'home',
        component: home,
        children: [{
                path: '',
                name: 'newsgs',
                component: newsgs,
            },
            {
                path: 'aboutus',
                name: 'aboutus',
                component: aboutus,
            },
            {
                path: 'companygy',
                name: 'companygy',
                component: companygy,
            },
            {
                path: 'companyzxns',
                name: 'companyzxns',
                component: companyzxns,
            },
            {
                path: 'companyry',
                name: 'companyry',
                component: companyry,
            },
            {
                path: 'intro',
                name: 'intro',
                component: intro,
            },
            {
                path: 'newsgs',
                name: 'newsgs',
                component: newsgs,
            },
            {
                path: 'newshy',
                name: 'newshy',
                component: newshy,
            },
            {
                path: 'beforeEighteen',
                name: 'beforeEighteen',
                component: beforeEighteen,
            },
            {
                path: 'nineteen',
                name: 'nineteen',
                component: nineteen,
            },
            {
                path: 'productsjs',
                name: 'productsjs',
                component: productsjs,
            },
            {
                path: 'productsspjs',
                name: 'productsspjs',
                component: productsspjs,
            },
            {
                path: 'productsys',
                name: 'productsys',
                component: productsys,
            },
            {
                path: 'techlq',
                name: 'techlq',
                component: techlq,
            },
            {
                path: 'techzlrz',
                name: 'techzlrz',
                component: techzlrz,
            },
            {
                path: 'techzm',
                name: 'techzm',
                component: techzm,
            },
            {
                path: 'aboutus',
                name: 'aboutus',
                component: aboutus,
            }
        ]
    }]
})


